 <!--这是分享页 --> 

<template>
  <div class="link">
     
      <div class="link-top">
          <div class="opabox"></div>
          <div class="link-tb">
            <img id="one" src="../assets/jiupailogo_100.png" alt="">
            <span>九拍陪你练</span>
            <button @click="one">立即下载</button>
          </div>
      </div>
      <div class="link-main">
        <img src="../assets/logo.png" alt="" id="clear">
        <div class="main">
            <h4>天线宝宝</h4>
            <span>18人在线</span>
            
        </div>
        <button @click="two">关注</button>
      </div>

      <div class="link-end">
          <input type="text" placeholder="   说点什么吧...">
      </div>
  </div>
</template>


<script>
    
export default {
  name: 'Link',
  data () {
    return {
      
    }
  },
  components:{
      
  },
  methods:{
      one:function(){
          alert(1)
      },
      two:function(){
          alert(2)
      }
  }
}
  
</script>

<style scoped lang='scss'>
    .link{
        width: 100%;
        height: 100%;
       background: url("../assets/testimg.JPG");
        .link-top{
            width: 100%;
            height: 70px;
            position: relative;
            .opabox{background-color: rgba(0, 0, 0, 0.7);position:absolute;left:0;top:0;width: 100%;
            height: 70px;}
            .link-tb{
                width: 100%;
                display: flex;
                justify-content: space-between;
                position:absolute;
                #one{
                    display: block;
                    margin: 10px 10px 10px 15px;       
                    width: 0.5rem;
                    height: 0.5rem;

                }
                span {
                    color: white;
                    width: 100%;
                    height: 25px;
                    margin: 22.5px 106px 22.5px 0px;
                }
                button{
                    width: 100%;
                    border: 1px solid #000;
                    
                    font-size: 1.8vmax;
                    color: #FFFFFF;
                    background: #DA6666 ;
                    line-height: 36px;
                    margin: 15px 15px 15px 0;
                    padding: 0;
                }
          }
        }
        .link-main{
            display: flex;
            width: 142px;
            height: 30px;
            background: rgba(0,0,0,0.60);
            border-radius: 100px;
            margin: 22px 0 0 15px;
            #clear{
                width: 24px;
                height: 24px;
                border-radius:5px;
                margin: 5px 0 0 5px ;
            }
            .main{
                width: 100%;
                margin: 0 0 0 5px;
                h4{
                margin: 0;
                padding: 0;
                font-size: 12px;
                color: #FFFFFF;
                line-height: 12px;
                
                }
                span{
                    
                    font-size: 8px;
                    color: #FFFFFF;
                    line-height: 10px;
                    font-weight:100; 
                }
                
            }
            button{
                    
                    width:100%;
                    height: 20px;
                    margin: 5px;padding: 0;
                    background: #DA6666;
                    border-radius: 10px;
                    border: none;
                    color: white;
                    font-size: 10px;
                    font-weight:100; 
                }
        }
        .link-end{
            width: 100%;
            height: 49px;
            background: #333333;
            position: fixed;
            bottom: 0; 
            text-align: center;
            line-height: 49px;
            input{
                background: #333333;
               border: 1px solid #DDDDDD;
                border-radius: 6px;
                width: 80%;
                height: 30px;
                color: #999999;
            }
        }
    }
</style>



