import Vue from 'vue'
import Router from 'vue-router'
import Details from "@/components/Details"
import Link from "@/components/App_Link"
import Past from "@/components/App_Past-main.vue"
import Validation from "@/components/App_Validation-main"
import Living from "@/components/App_Living-main"

Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',  // 直播列表页
      name: 'Details',
      component: Details
    },
    {
      path:'/link', // 分享页
      name:'link',
      component:Link
    },
    {
      path:'/past', // 回顾
      name:'past',
      component:Past
    },
    {
      path:'/validation/:preId',
      name:'Validation',
      component:Validation
    },
    {
      path:'/living/:id',
      name:'Living',
      component:Living
    }
  ]
})



