<!--这是往期回放-->
<!--  status 2-->

<template>
<div>


  <div class="apppast" v-for="(items,index) in playback" :key="items.id">
    <p>往期回顾</p>
    <div class="living">
      <div class="living-bg" @click="tabChange">
          <div class="txts">
            <span>{{items.title}}</span>
            <h6>{{items.pre_time}}</h6>
          </div>
           <h5>
               <img src="../assets/1510207889636.JPG">
              <span>{{items.pre_time}}</span>
          </h5>
          <img src="../assets/1510207889636.JPG">
      </div>
    </div>
  </div>
</div>
</template>

<script>

// import {mapGetters, mapActions} from 'vuex';


export default {
  name: 'Apppast',
  props: ['playback'],
  data () {
    return {
      
    }
  },
  methods:{
    tabChange(){  // 路由跳转
		  this.$router.push({path: "past"})
	  }
  },
  mounted:function(){
    
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
 #wrap{
    background:#fff;
  }
  p{
    padding:0.1rem 0 0.3rem 0.1rem;
    margin: 0;
    width: 1.12rem;
    height: 0.32rem;
    font-size:0.16rem;
    color:#000;
    font-weight:600;
  }



   .apppast{
    background: white;
    margin: 0;
    padding: 0 0 11px 0;
    
    p{
        padding:0.1rem 0 0.3rem 0.1rem;
        margin: 0;
        width: 1.12rem;
        height: 0.32rem;
    }
    .living{
      
      
      .living-bg{
        background: #feffff;
        padding: 0 10px 0 10px;
        width: 100%;
        height: 2rem;
        overflow: hidden;
        opacity:0.9;
        position: relative;
        position:relative;
        img{
          border:none;
          width: 100%;
          top:0;
          height: 100%;
        }
        .txts{
          position: absolute;
          top: 70px;
          color: white;
          vertical-align:middle;
          text-align: center;
          margin-left: 27%;
          span{
            font-size:0.24rem;
            font-weight:600;
            text-align: center;
          }
          h6{
            text-align: center;
            margin-top: 5px;
            padding: 0;
            font-size:0.14rem;
          }
        }
         h5{
          position:absolute;
          left:0.2rem;
          bottom:0.3rem;
          height:0.2rem;
          font-weight:400;
          margin:0; 
          img{
            width:0.2rem;
            height:100%;
            border-radius:50%;
            margin-bottom: -0.05rem;
          }
          span{
            font-size:0.12rem;
            color:#fff;
          }
        }
      }
    }
  }
</style>



