<template>
<div id="wrap">
<div class="app_section">
  <div class="app_sec_ban">
    <!-- <video :src="video" controls="controls" autoplay="autoplay" ></video> -->
    <div id="id_test_video" style="width:100%; height:100%;margin:0 auto" ></div>
      
    <div class="app_sec_ban_people">
      <img :src="pict" alt="">
      <div class="font-size">
        <p>{{text}}</p>
        <p>{{peop}}人在线</p>
      </div>
      <button @click="download">
        {{clear}}
      </button>
    </div>
  </div>
<div class="app_box">
    <div class="app_sec_talk" >
    <ul>
        <li>欢迎来到直播间！！！</li>
        <li v-for="(item, index) in speaks" :key="index">
          <span>{{item.name}}说: </span> {{item.content}}
        </li> 
       
      </ul>
    </div>
    <footer class="app_footer" @click="download">
      <input type="text" placeholder="说点什么吧...">
    </footer>
    
  </div>
</div>
</div>
</template>
<script>

import axios from "axios"
import Headers from "./Share-head.vue"
import qs from "qs"

export default {
name: "LivingMain",
data: function(){
  return {
    speak: [],
    id: "",
    video:"",
    text:"",
    clear:"关注",
    peop:"",
    pict:"",
    livespeak:[],
    jiazai: require("../assets/jiazai_bg.png"),
    speaks:[],
    speakname:[],
    preTime:0,
    preId:"",
    time:null
  }
},
components:{
Headers
},
methods:{
  newTcPlayer: function(){  // 回调函数
     // 将视频用腾讯云视频格式解码
    var player = new TcPlayer('id_test_video', {  
      "m3u8": this.video, //请替换成实际可用的播放地址
      "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
      "coverpic" : this.jiazai, // 添加一个视频播放前的图片
      "controls": "none", // 取消控件
    });
    player.pause()
    player.play()

  },
  tabchange:function(){
    this.$router.push({path:'living'}) 
  },

  linkafter:function(){   // 获取直播间信息
  // 直播间的接口:http://testapi.9beats.com/get_live_info_web   
  axios({
        url: 'http://testapi.9beats.com/get_live_info_web?id='+this.id,
        method: 'post',
        data: {
          relevanceId:this.$route.params.id,
          pictureType:4
        },
        transformRequest: [function (data) { // 转换成fromdata格式
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
  })
  .then(this.GetdateSucc.bind(this))
  .catch(this.GetdateErr)
  },
  GetdateSucc:function(res){
    // console.log(res.data.data.id)
    this.text = res.data.data.username;
    this.video = res.data.data.url;
    this.peop = res.data.data.live_status
    this.pict = res.data.data.headimg;
    this.preId = res.data.data.id
    this.newTcPlayer();
  },
  GetdateErr:function(error){
    console.log("获取直播视频出错")
  },

// 这是获取弹幕接口
  linkspeak:function(){
    var that = this;
    axios({
      url: 'http://testapi.9beats.com/get_sub_user_comment',
      method: 'post',
      data: {
       // liveID:5,
        liveID:this.preId,        
        preTime:this.preTime
      },
      transformRequest: [function (data) {
        let ret = ''
        for (let it in data) {
          ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
        }
        return ret
      }],
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
    .then(function(res){
      console.log(res.data)
      // console.log(that.speakname,'123')
      that.speak = res.data.data
      that.preTime = res.data.message
       for (var i = 0; i < 5; i++) {
          that.speaks.push(that.speak[i]);
        };
        var num=1
        that.time=setInterval(function(){
          if(num<res.data.data.length){
            num=1
          }else{
            clearInterval(that.time)
          }
          that.speaks.push(res.data.data[num])
          that.speaks.shift(that.speaks[4])
        },1000)
    })
    .catch(function(erres){
      console.log("获取接口数据报错")
    })
  },
// 跳转下载接口
  download:function(){ 
      location.href="http://a.app.qq.com/o/simple.jsp?pkgname=com.ken.ninebeats";
  },

},

beforeMount: function(){
  this.id = this.$route.params.id;
  this.linkafter();
  this.see = true
  return false
},
mounted:function(){
  var that = this;
    setInterval(function(){ // 执行弹幕请求
      clearInterval(that.time)
      that.linkspeak()
  },10000);

  // 横屏刷新
  function hengshuping() {
     if (window.orientation == 180 || window.orientation == 0) {
          location.reload()        
          // return false 
      }
     if (window.orientation == 90 || window.orientation == -90) {
          
          // alse(this.see)
          location.reload() 
          // return false
          
      }
  }
 window.addEventListener("onorientationchange" in window ? "orientationchange" : "resize", hengshuping, false);

 
  
}, 
updated: function(){

}
}
</script>
<style scoped lang='scss'>

* {
margin: 0px;
padding: 0px; 
text-decoration:none;
box-sizing:border-box;
font-size:0.14rem;}

ul, li {
list-style: none; }

html {
-webkit-touch-callout: none;
-webkit-user-select: none;
-o-user-select: none;
-moz-user-select: none;
-ms-user-select: none;
user-select: none;
font-size: 625%; }

img, div, body, html, ul, li, a {
-webkit-touch-callout: none; }

input {
-webkit-appearance: none; }
a{
text-decoration:none;
color:#ccc;
}
html,body,#wrap{
width:100%;
height:100%;

}
#wrap{
width:100%;
height:100%;
display:flex;
flex-direction:column;
position:relative;
overflow: scroll;
.app_section{
  width:100%;
  height:100%;
.app_sec_ban{
  overflow: hidden;
  width:100%;
  height:100%;
  #id_test_video{
  position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    .vcp-player .touchable .vcp-playing{
      width: 100%;
      height: 100%;
    }
    .vcp-error-tips{
      margin: 0;padding: 0;
    }
    .vcp-loading{
      margin: 0;padding: 0
    }
  }
  

  .app_sec_ban_people{
  overflow: hidden;
  width:1.42rem;
  background:rgba(0,0,0,0.6);
  border-radius:0.3rem;
  position: absolute;
  top: 0.05rem;
  left:0.05rem; 
  display: flex;
  justify-content:space-between;
  align-items:center;
  padding:0 0.05rem;
  img{
    width:0.2rem;
    height:0.2rem;
    border-radius:50%;
    float: left;
    margin-right:0.05rem;
  }
.font-size{
  text-align: left;
  margin-left: -0.15rem;
  p{
    color:#fff;
    font-size:0.12rem;
    font-weight:900;
    padding-top: 0.02rem;
    &:nth-child(2){
      font-size:0.1rem;
      display: block;
      font-weight:400;
      padding:0;
  }
}
}
  button{
    width:0.4rem;
    height: 0.2rem;
    border:0;
    border-radius:0.2rem;
    background:#da6666;
    color:#fff;
    font-size:0.1rem;
  }
  }
}  

}
.app_box{
  position: fixed;
  bottom: 0;
  right:0;
  width:100%;
  height:30%;
  .app_sec_talk{
    width: 94%;
    height:70%;
    margin-left:3%;
    margin-top:0.1rem;
    overflow: hidden;
    ul{
      width:100%;
      height:100%;
      overflow: hidden;
      li{
        width:100%;
        height:0.24rem;
        line-height:0.24rem;
        font-size:0.14rem;
        color:#fff;
        i{
          color:#fff;
          font-style:normal
        }
        span{
          color:orange;
        }
        }
    }
    
  }
  .app_footer{
    width:100%;
    height:0.5rem;
    padding:0.1rem 0.1rem ;
    position: absolute;
    bottom:0;
    left:0;
    input{
      width:100%;
      height:100%;
      border:0;
      background: transparent;
      border:1px solid #dddddd;
      border-radius:0.03rem;
      padding-left:0.1rem;
    }
    }
  }
}
/* 横屏*/
@media screen and (orientation: landscape) {
    .app_box{
      display: none
    }
}


</style>




