
<!--这是预告页的详情页-->

<template>
  <div id="vaildationmain">
    <section class="vai_section">
    	<div class="section_ban">
    		<img :src="img" alt="">
    	</div>
    	<div class="section_info">
    		<h6>
    			<span>直播时间</span>
    			<p>
    				{{newday}}
    			</p>
    		</h6>
    		<h6>
    			<span>活动费用</span>
    			<p>
    				免费
    			</p>
    		</h6>
    		<h6>
    			<span>参与人数</span>
    			<p>
					<a>{{user_grade}}</a>人已报名
    			</p>
    		</h6>
    	</div>
		<div class="section_message">
			<h1><i>活动介绍板块，支持自由排版。颜色、添加图片 </i>换行</h1>
			<h2>空行 添加图片</h2>
			<h3><em>字号</em>与加粗</h3>
			<h4>支持左对齐、右对齐、居中对齐、两头对其等排版方式。内容字数不受限制。相当于在后台布置一个自由度较高的文本编辑框。支持左对齐、右对齐、居中对齐、两头对其等排版方式。内容字数不受限制。相当于在后台布置一个自由度较高的文本编辑框。支持左对齐、右对齐、居中对齐、两头对其等排版方式。内容字数不受限制。相当于在后台布置一个自由度较高的文本编辑框。</h4>
		</div>
    </section>
    <footer class="vai_footer">
		立即报名
	</footer>
  </div>
</template>

<script>

// import {mapGetters, mapActions} from 'vuex';
import Head from "./Share-head.vue"

export default {
  name: 'Vaildationmain',
	props: ['playback'],
  data () {
    return {
	  day:"2017-11-2",
	  preData: {},
	  newday:"",
	  img:"",
	  user_grade:"",
	  
    }
  },
  components:{
    
  },
  created () {
		// console.log(localStorage.getItem('data'))
		const data=JSON.parse(localStorage.getItem('data'))[0]
		this.newday=JSON.parse(localStorage.getItem('data'))[0].pre_time.replace(/-/g,'/')
		this.img=JSON.parse(localStorage.getItem('data'))[0].cover
		this.user_grade=JSON.parse(localStorage.getItem('data'))[0].user_grade
		console.log(data)
	},
  mounted(){
	  var that = this;
	  // console.log(111, this.$route.params.preId);
	  // console.log(444, JSON.parse( localStorage.getItem("preparation") ));
	  var preId =  this.$route.params.preId;
	  var preArr = JSON.parse( localStorage.getItem("preparation") );
	  preArr.forEach(function(item, index, arr){
		  if(item.id == preId){
			  that.preData = item;
			  console.log(555, that.preData);
		  }
	  })
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
  * {
	margin: 0px;
	padding: 0px; 
	text-decoration:none;
	box-sizing:border-box;
	font-size:0.14rem;
	font-weight: 400;
}
i{
	font-style:normal;
}
span,p{
	color:#636363;
}
h1,h2,h3,h4,h5,h6{
	color:#636363;
}
html,body,#vaildationmain{
	width:100%;
	height:100%;
	overflow:hidden;
}
#vaildationmain{
	display:flex;
	flex-direction:column;
	position:relative;
	.vai_section{
			overflow-y:scroll;
			margin-bottom: 0.29rem; 
			width:100%;
			height:100%;
			position: relative;
		.section_ban{
			width:100%;
			height:57%;
			img{
				width:100%;
				height:100%;
			}
		}
		.section_info{
			width:94%;
			background:#fff;
			position: absolute;
			left:3%;
			top:40%;
			border-radius:0.1rem;
			border:1px solid #eee;
			padding:0 0.15rem 0 0.15rem;
			h6{
				border-bottom:1px solid #eee;
				background:#fff;
				span{
					padding-top: 0.1rem;
					display: inline-block;  
					font-size:0.12rem;   
				}
				p{
					line-height:0.34rem;
					font-size:0.12rem;
				}
				&:last-child{
					border-bottom:none;
				}
			}
		}
		.section_message{
			margin-top: 23%;
			width:94%;
			height:auto;
			margin-left:3%;
			h1{
				
				i{
					color:red;
				}
			}
			h2{
				line-height:0.36rem;

			}
			h3{
				padding-bottom:0.05rem;
				em{
					font-size:0.16rem;
					font-style:normal;
				}
			}
			h4{
				line-height: 0.2rem;
			}
		}
	}
	.vai_footer{
		position:absolute;
		left:0;
		bottom:0;
		width:100%;
		height:0.29rem;
		background:#da6666;
		line-height:0.29rem;
		text-align:center;
		color:#fff;
		font-size:0.12rem;
	}
}
/*在iPhone4 以下 */
@media screen and (max-width: 320px) {
    .section_message{
    	h1{
    		padding-top:0.15rem;
    	}
    }
}
</style>



