<!--这是列表页-->

<template>
  <div class="details">
    <Appliving :living="living"></Appliving>
    <Vaildation :preparation="preparation"></Vaildation>
    <!-- <Apppast :playback="playback"></Apppast> -->
  </div>
</template>

<script>
import Vaildation from "./App_Validation"
import Applink from "./App_Link"
import Apppast from "./App_Past.vue"
import Appliving from "./App_Living.vue"
import axios from 'axios'

export default {
  name: 'Details',
  data () {
    return {
      username:"",
      password:"",
      living:[], // 直播状态
      playback:[],// 回放状态
      preparation:[]// 预约状态
    }
  },
  components:{
    Vaildation,Applink,Apppast,Appliving
  },
  methods:{
    test:function(){
      // http://testapi.9beats.com/home_live_list   参数：userid、page
     
      axios({
            url: 'http://testapi.9beats.com/home_live_list ',
            method: 'post',
            data: {
              userid: this.username,
			    	  page: this.password
            },
            transformRequest: [function (data) {
              let ret = ''
              for (let it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
      })
      .then(this.GetdateSucc.bind(this))
      .catch(this.GetdateErr)
    },
    GetdateSucc:function(res){  // 传递 直播信息
      // console.log(res)
      this.living = res.data.liveing;
      this.playback = res.data.playback;
      this.preparation = res.data.preparation;
      localStorage.setItem('data',JSON.stringify(this.preparation))
    },
    GetdateErr:function(error){
      console.log("获取后直播列表数据失败")
    },  
  },
  mounted: function(){
    this.test();
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
  .details{
   background: #ecf0f3;
  }
  
</style>



